@import url(http://fonts.googleapis.com/css?family=Open+Sans:300italic,400italic,700italic,400,300,700);

$L_font: 12px Open Sans;
$l_font: 11px Open Sans;
$R_font: 15px Open Sans;
$dropdown_font: 13px Opes Sans;
$font_logo: 27px Open Sans;
$head_color: rgb(68, 140, 203);
$main_color: rgb(255, 255, 255);
$hover_color: rgb(0, 0, 0);

body {
    background-color: $main_color;
    margin: 0; }


#header {
    background-color: $head_color;
    width: 100%;
    height: 130px;
    margin: 0;

    a {
        text-align: center; } }


.header-upper {
  float: left;
  margin-top: 1%;
  margin-left: 7.7%;
  margin-bottom: 2%;
  width: 92.3%;

  a {
       padding-right: 0.75%;
       padding-left: 0.5%;
       text-decoration: none;
       font: $L_font;
       text-transform: uppercase;
       text-align: center;
       color: $main_color;
       border-right: 1px solid $main_color; }

  a:hover {
       text-decoration: underline; }

  #first {
      padding-left: 0; }

  #last {
      padding-right: 0;
      border-right: none; } }


.header-right {
  float: right;

  a {
    float: left;
    padding: 10px;
    text-decoration: none;
    font: $R_font;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
    color: $main_color;

    &:hover {
        color: $hover_color; } } }

.dropdown {
    float: left; }

.dropdown-content {
    display: none; }

.dropdown-content {
  display: none;
  position: absolute;
  background-color: $hover_color;
  min-width: 100px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;

  ul, li {
    list-style: none;
    padding: 0;
    margin: 0; }

  ul {
   &:before {
    content: "";
    position: absolute;
    right: 50px;
    top: -10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 10px 10px 10px;
    border-color: transparent transparent $hover_color transparent;
    z-index: 1; } }

  a {
    width: 100px;
    color: $main_color;
    font: $dropdown_font;
    font-weight: 600;
    text-decoration: none;
    display: block; } }

.dropdown-content a:hover {
  background-color: $main_color; }

.dropdown:hover {
  .dropdown-content {
    display: block;
    margin-top: 50px;
    margin-left: -25px; } }


.logo {
  float: left;
  font: $font_logo;
  text-transform: uppercase;
  text-decoration: none;
  color: $main_color;
  margin-left: 7.6%; }

.logo:hover {
    color: $hover_color; }

#bold {
    font-weight: 900; }

#light {
    font-weight: 100; }




@media screen and ( max-width: 600px  ) {
    #header {
        height: fit-content; }

    .header-upper {
        margin-left: 0;
        text-align: center;
        width: 100%;

        a {
         padding-right: 1.25%;
         font: $l_font; } }
    .header-lower {
         a {
          float: none;
          display: block; } }

    .header-right {
        float: none;
        text-align: center; }

    .logo {
        margin-right: 7%; }

    .dropdown {
        float: none;
        margin: 0 auto; }

    .dropdown:hover .dropdown-content {
        margin-left: 33%;
        margin-top: 0%; } }
